import React, {Component} from 'react';
import Feeds from '../Utils/Feed';
import Search from './Search';
import classes from './Feed.module.css';
import { connect } from 'react-redux';
import { Details, Save } from '../Store/Action/Details.action';
import Detailed from './Details';
import auth0 from '../Authentication/auth';
import auth from '../Authentication/auth';


const authI = new auth();


class Feed extends Component{

    
    componentDidMount = () => {

        const auth = new auth0();
        auth.isAuthenticated() ? this.render() : auth.login()
        
    }
    

    state = {
        detail: '',
        save: ''
    }

        
    

    updateDetails = (item) => {
        this.setState({
            detail: item
        })


        setTimeout(()=>{
            this.props.itemToUpdate(this.state.detail);
        }, 2000)

        
    }

    removeDetail = () => {
        this.setState({
            detail: ''
        })
    }

    saveItem =(item) => {


        this.setState({
            save: item
        })

        setTimeout(()=>{
            this.props.itemToSave(this.state.save)
        }, 2000)
        
    }

    

    

   
  
    feeds = Feeds;


    
    



    feed = this.feeds.map((item, index)=>{
        
            return(
                <div key={index} className={classes.feed} >
                    <h2 className={classes.title} onClick= {() => { this.updateDetails(item); }}>{item.title} <span>{item.price}</span></h2>
                    <div className={classes.feed_details}>
                        <h4>{item.company}</h4>
                        <h4>{item.location}</h4>
                        <h4>{item.position}</h4>
                        <p>{item.Summary}</p>
                    </div>
                    <div>
                        <button onClick= {() => { this.saveItem(item); }} className={classes.act}>Save</button>
                    </div>
                </div>
            )
        
        
    })

    
    

    

    render() {
        return(
            <article className={classes.feed_article}>
                
                <h2 className={classes.greetings}>Hi! <span>{authI.profile().given_name}</span></h2>
                <section className={classes.feed_section}>
                    <div className={classes.search_box}>
                        <Search className={classes.search}/>
                    </div>
                    
                    <div className={classes.feed_block}>
                        {this.feed}
                        
                    </div>

                        
                    
                </section>
                
                        {this.state.detail !== '' ? <div>
                               <button className={classes.remove} onClick={()=>{ this.removeDetail()}}>&times;</button><Detailed />
                           </div> : null}

                
            </article>
        )
    }
   
           
}

const mapStateToProps = (state) => ({
    searchResult: state.search_item
})

 const mapDispatchToProps=(dispatch)=>({
    itemToUpdate: item => dispatch(Details(item)),
    itemToSave: item => dispatch(Save(item))
})



export default connect(mapStateToProps, mapDispatchToProps)(Feed);
