export const Details = (item) => ({
    type: 'ITEM_DETAILS',
    payload: item
})

export const Save = (item) => ({
    type: 'ITEM_SAVE',
    payload: item
})

export const unSave = (item) => ({
    type: 'ITEM_DELETE',
    payload: item
})

export const search = (item) => ({
    type: 'SEARCH_ITEM',
    payload: item[0]
})