import React, { Component } from 'react';
import { connect } from 'react-redux';
import classes from './Feed.module.css';
import classe from './Save.module.css';
import Detailed from './Details';
import {Details, unSave} from '../Store/Action/Details.action';
import Apply from './Apply';
import auth0 from '../Authentication/auth';


class Saved extends Component{

    componentDidMount() {
        const auth = new auth0();
        auth.isAuthenticated() ? this.render() : auth.login()
    }

    auth = new auth0();

    state = {
        detail: '',
        apply: false
    }

    apply =() =>{
        this.setState({
            apply: true
        })
    }

    hideApply =() =>{
        this.setState({
            apply: false
        })
    }

    updateDetails = (item) => {
        this.setState({
            detail: item
        })

        setTimeout(()=>{
            this.props.itemToUpdate(this.state.detail);
        }, 2000)
    }

    unsaveItem =(item) => {
        

        setTimeout(()=>{
            this.props.itemToUnsave(item)
        }, 2000)
        
    }

    removeDetail = () => {
        this.setState({
            detail: ''
        })
    }
     seave = () => {
         if(JSON.parse(window.localStorage.getItem('items')) !== null) {
            let inStore = JSON.parse(window.localStorage.getItem('items'));
            let save = inStore.map((item, index) => {
                return(
                    <div key={index} className={classes.feed} >
                <h2 className={classes.title} onClick= {() => { this.updateDetails(item); }}>{item.title} <span>{item.price}</span></h2>
                <div className={classes.feed_details}>
                    <h4>{item.company}</h4>
                    <h4>{item.location}</h4>
                    <h4>{item.position}</h4>
                    <p>{item.Summary}</p>
                </div>
                <div>
                <button onClick= {() => { this.unsaveItem(index); } } className={classe.delBtn}>Delete</button>
                </div>
            </div>
        
                       
                )
            })
            return save;
         } else{
            let save = <h2 className={classe.no_save}>You haven no Job saved</h2>
            return save;
         }

         
     }
    

     
     
      
    render() {
        
        return(
            <div>
                <div className={classe.save_article}>
               {this.seave() }
               {
                this.state.detail ? <div className={classes.okPull}>
                {this.state.detail !== '' ? <div >
                    <button className={classes.remove} onClick={()=>{ this.removeDetail()}}>&times;</button><Detailed /><button className={classes.apply} onClick={()=>{
                        this.apply()
                    }}>Apply</button>
                </div> : null}


                {this.state.apply == true? <div>
                    <Apply /><button className={classes.hideApply} onClick={()=>{ this.hideApply()}}>&times;</button>
                </div> : null}
                
                
                </div> : null
                }
            </div>

                <div>
                
                </div>

            </div>
            
        )
    }
}



const mapDispatchToProps=(dispatch)=>({
    itemToUpdate: item => dispatch(Details(item)),
    itemToUnsave: item => dispatch(unSave(item))
})

export default connect(null, mapDispatchToProps)(Saved)