import classes from './Login.module.css';
import React, { Component } from 'react';
import {Route, Link, Switch} from 'react-router-dom';
import Auth from '../Authentication/auth';

const auth = new Auth();

class Login extends Component{

    componentDidMount = () => {

        const auth0 = new Auth();
        auth0.handleAuth();
    }

    render() {
        
        return(
            <div className={classes.logArea}>
                <h2>Welcome To App Name</h2>
                <p>You are few steps to getting your dream Job.<br />Click on the button below to get started</p>
                {
                    auth.isAuthenticated() ? <button onClick={()=>{window.location.pathname="/jobsite/feed"}}>Explore </button> :  <button onClick={()=>{auth.login()}}>Click To Login </button>
                }
               
            </div>
        )
    }
}

export default Login;