import { applyMiddleware, combineReducers, createStore } from 'redux';
import logger from 'redux-logger';
import detailsReducer from './Reducer/Details.reducer';
import saveReducer from './Reducer/Save.reducer';
import deleteSave from './Reducer/Delete.reduce';

const reducers = combineReducers({
    detail_reducer: detailsReducer,
    save_reducer: saveReducer,
    delete_saved: deleteSave
});

export const store = createStore(reducers, applyMiddleware(logger));