import React from 'react';
import {Route, Link, Switch} from 'react-router-dom';
import classes from './Footer.module.css';
import auth0 from '../Authentication/auth';

const auth = new auth0();

const Footer =()=>{
    return(
        <div>
        <div className={classes.search_text}> 
        <div className={classes.container}>
          <div className={[classes.row, classes.text_center].join(' ')}>
          
            <div className="form">
                <h4>SIGN UP TO OUR NEWSLETTER</h4>
                 <form id="search_form" className={["form_search, form_horizontal"].join(' ')}>
                     <input type="text" className={classes.input_search} placeholder="Email Address" />
                     <button type="submit" className={classes.btn_search}>SUBMIT</button>
                 </form>
             </div>
         
           </div>         
        </div>     
     </div>
     
     <footer>
      <div className={classes.container}>
        <div className={classes.row}>
                 <div className={[classes.col_md_3, classes.col_sm_6, classes.col_xs_12].join(' ')}>
                   <span className={classes.logo}><Link to='/jobsite/' className={classes.logo}>LOGO</Link></span>
                   
                 </div>
                 
                 <div className={[classes.col_md_3, classes.col_sm_6, classes.col_xs_12].join(' ')}>
                     
                      <ul className={classes.menu}>
                        <span>Quick Link</span>
                            <li>
                                <Link to='/jobsite/feed'>Feed</Link>
                            </li>
                            <li>
                                <Link to='/jobsite/saved'>Saved</Link>
                            </li>
                            {
                                auth.isAuthenticated() ? 
                                <li>
                                <Link to='/jobsite/' onClick={()=>{auth.logout()}}>Logout</Link>
                            </li>: <li>
                                <Link to='/jobsite/' onClick={()=>{auth.login()}}>Login</Link>
                            </li> 
                            }
                            
                            
                        </ul>
                 </div>
                 
            
                 <div className={[classes.col_md_3, classes.col_sm_6, classes.col_xs_12].join(' ')}>
                   <ul className={classes.address}>
                     <span>Contact</span>       
                     <li>
                        <i className={["fa fa-phone", classes.fa].join(' ')} aria-hidden="true"></i> 
                        <a href="#">Phone</a>
                     </li>
                     <li>
                        <i className={["fa fa-map-marker", classes.fa].join(' ')} aria-hidden="true"></i> 
                        <a href="#">Adress</a>
                     </li> 
                     <li>
                        <i className={["fa fa-envelope", classes.fa].join(' ')} aria-hidden="true"></i> 
                        <a href="#">Email</a>
                     </li> 
                    </ul>
                 </div>
             </div> 
         </div>
     </footer>
     </div>
    )
}

export default Footer;