import React, { Component } from 'react';
import {Route, Link, Switch} from 'react-router-dom';
import classes from './Header.module.css';
import auth0 from '../Authentication/auth';

const auth = new auth0();


class Header extends Component{

    

     menu = <div className={classes.sideMenu}>
         <button onClick={()=>{this.hideSide()}}>&times;</button>
         <ul >
            <li>
                <Link to='/jobsite/feed'>Feed</Link>
            </li>
            <li>
                <Link to='/jobsite/saved'>Saved</Link>
            </li>
            {
                                auth.isAuthenticated() ? 
                                <li>
                                <Link to='/jobsite' onClick={()=>{auth.logout()}}>Logout</Link>
                            </li>: <li>
                                <Link to='/jobsite' onClick={()=>{auth.login()}}>Login</Link>
                            </li> 
                            }
        </ul>
     </div>;

     state = {
         menuShow: false
     }
    
     showSide = () =>{

         this.setState((prevState, prevProps) => {
             return {menuShow: !prevState.menuShow}
         })
     }

     hideSide = () =>{
        this.setState((prevState, prevProps) => {
            return {menuShow: !prevState.menuShow}
        })
     }

    render() {
        return(
            <div>
                <header>
                    <nav className={classes.nav}>
                        <div className={classes.menu_logo}>
                        <button onClick={()=>{
                        this.showSide();
                        }}><i className='fas fa-bars'></i></button>
                            <Link to='/jobsite/' className={classes.logo}>LOGO</Link>
                        </div>
                        
                        <div className={classes.right_head}>
                            {this.state.menuShow === true ? this.menu : null}
                            <ul className={classes.desktopMenu}>
                            <li>
                                <Link to='/jobsite/feed'>Feed</Link>
                            </li>
                            <li>
                                <Link to='/jobsite/saved'>Saved</Link>
                            </li>
                            {
                                auth.isAuthenticated() ? 
                                <li>
                                <Link to='/jobsite/' onClick={()=>{auth.logout()}}>Logout</Link>
                            </li>: <li>
                                <Link to='/jobsite/' onClick={()=>{auth.login()}}>Login</Link>
                            </li> 
                            }
                            
                            
                            </ul>

                            {auth.isAuthenticated() ? <img src={auth.profile().picture} alt={auth.profile().given_name} className={classes.profileImg}/> : null}
                            
                        </div>
                            
                </nav>
                </header>
            </div>
        )
    }
}

export default Header;