import React, {useState} from 'react';
import { connect } from 'react-redux';
import classes from './Details.module.css';
import Apply from './Apply';

function Detailed({details}) {
        console.log(details.Summary);
        let qualifications ;
        let quo = details.qualifications;

        let res = details.responsibilities;
        let responsibilities;

        let skil = details.skills;
        let skills;


        

        const [applyState, setApply] = useState(false);

        if( quo){
                qualifications = quo.map((item, index)=> {
                       return <li key={index}>{item}</li>
                })
        }

        if(res){
                console.log(res);
                responsibilities = res.map((item, index)=> {
                       return <li key={index}>{item}</li>
                })
        }

        if(skil){
                skills = skil.map((item, index)=> {
                       return <li key={index}>{item}</li>
                })
        }
        

        
        return(
            
                
                <article className={classes.detail_box}>
                        
                        <section className={classes.job_info}>
                                <h2>Job Information</h2>
                                <h4>{details.title}</h4>
                                <h4>{details.company}</h4>
                                <h5>{details.type}</h5>
                        </section>

                        <h2>Job Description</h2>
                        <section className={classes.job_description}>
                                
                                <div>
                                <h4>Job Summmary</h4>
                                <p>{details.Summary}</p>
                                        <h4>Quolifications and Requirement</h4>
                                        <ul>
                                                {qualifications}
                                        </ul>
                                         
                                        <h4>Skills and Competency</h4>
                                        <ul>
                                                {skills}
                                        </ul>
                                </div>
                                
                                <div>
                                        
                                        

                                        <h4>Key Responsibilities</h4>
                                        <ul>
                                                {responsibilities}
                                        </ul>
                                        <i className={classes.expires}>Deadline:<strong>{details.deadline}</strong></i>
                                        
                                </div>

                                
                                
                                

                               {
                                       applyState == true ? <div>
                                               <button className={classes.hideApply} onClick={()=>{
                                        setApply(false);
                                    }}>&times;</button> <Apply />
                                       </div> : null
                               }
                               
                        </section>


                        <button className={classes.apply} onClick={()=>{
                                   setApply(true);
                               }}>Apply</button>

                        
                        <div className={classes.bottomSpace}>
                                        
                        </div>
                        
                </article>
        ) 
}

const mapStateToProps = (state) => ({
    details: state.detail_reducer
})
export default connect(mapStateToProps)(Detailed);