import React, {Component} from 'react';
import { connect } from 'react-redux';
import classes from './Search.module.css';
import {search} from '../Store/Action/Details.action'



class Search extends Component{


    

    render() {

        
        

        return(
            <section className={classes.search_section}>
                <form>
                    <legend>Filter Jobs</legend>
                    <label for="categories">Categories:</label>
                    <select id="categories" name="categories">
                        <option value="all">All</option>
                        <option value="UI/UX Design">UI/UX Design</option>
                        <option value="Frontend Development">Frontend Development</option>
                        <option value="Backend Development">Backend Development</option>
                    </select>
                    <label for="location">Location:</label>
                    <select id="location" name="location">
                        <option value="all">Tata</option>
                        <option value="Abia">Tata</option>
                        <option value="Imo">Tata</option>
                        <option value="Portharcourt">Tata</option>
                    </select>

                    <input type="submit" className={classes.search_btn}  value="Search"/>
                    
                </form>
            </section>
            
        )
    }
}



export default Search;