import React from 'react';
import classes from './Apply.module.css';
import { connect } from 'react-redux';
import auth from '../Authentication/auth'

const auth0 = new auth;

const Apply = ({details}) => {
    let property = details;
    return(
        <div className={classes.apply}>
            <h4>{property.title}</h4>
            <h4>{property.company}</h4>
            <h5>{property.type}</h5>

            <form method="post" action={property.email}>
                <div>
                    <label htmlFor="fullName">FullName: </label>
                    <input type="text" id="fullName" name="fullName" value={auth0.profile().name}/>
                </div>
                <div>
                    <label htmlFor="email">Email: </label>
                    <input type="email" id="email" name="email" value={auth0.profile().email}/>
                </div>
                <div>
                    <label htmlFor="country">Country: </label>
                    <select id="country" name="country">
                        <option value="Nigeria">Nigeria</option>
                        <option value="Gambia">Gambia</option>
                        <option value="Nigeria">Nigeria</option>
                        <option value="Gambia">Gambia</option>
                        <option value="Nigeria">Nigeria</option>
                        <option value="Gambia">Gambia</option>
                        <option value="Nigeria">Nigeria</option>
                        <option value="Gambia">Gambia</option>
                    </select>
                </div>
                <div>
                    <label htmlFor="number">Phone Number: </label>
                    <input type="number" id="number" name="number" />
                </div>
                <div>
                    <label htmlFor="qualification">Qualification: </label>
                    <select id="qualification" name="qualification">
                        <option value="o'level">o'level</option>
                        <option value="bsc">BSc</option>
                        <option value="hnd">HND</option>
                    </select>
                </div>
                <div>
                    <label htmlFor="cv">Upload CV: </label>
                    <input type="file" id="cv" name="cv" className={classes.file}/>
                </div>
                <div>
                    <label htmlFor="cover_letter">Cover Letter: </label>
                    <textarea id="cover_letter" name="cover_letter"></textarea>
                </div>
                
                <input type="submit" />
            </form>
            
        </div>
    )
}



const mapStateToProps = (state) => ({
    details: state.detail_reducer
})
export default connect(mapStateToProps)(Apply);